import React, { useState } from 'react'
import { RiBuilding2Line } from 'react-icons/ri'
import { FcDepartment } from 'react-icons/fc'
import { useNavigate, useParams } from 'react-router-dom'
import { TbWebhook } from 'react-icons/tb'
import { useContext } from 'react'
import { useEffect } from 'react'
import './assets/fonts/Font.css'
import { BsArrowBarLeft, BsArrowLeft, BsArrowRight } from 'react-icons/bs'
import Modal from 'react-modal'
import ObjectionRectificationTable from './ObjectionRectificationTable'
import ObjectionFormIndex from './ObjectionFormIndex'
import ObjectionForgery from './ObjectionForgery'
import { ImCross } from 'react-icons/im'
import ApplicationSubmitScreen from './ApplicationSubmitScreen'

// Modal
const customStyles = {
  content: {
    top: '50%',
    left: '50%',
    right: 'auto',
    bottom: 'auto',
    marginRight: '-50%',
    transform: 'translate(-50%, -50%)',
    backgroundColor: 'white',
    border: 'none'
  },
};
Modal.setAppElement('#root');

const ObjectionIndex = (props) => {

  const { id } = useParams()

  const navigate = useNavigate()

  const [index, setindex] = useState(0)
  const [modalIsOpen, setIsOpen] = useState(false);
  const [appId, setappId] = useState('')
  const [openSubmit, setopenSubmit] = useState(false)
  const [heading, setheading] = useState('')
  const [formName, setformName] = useState('')

  const openModal = () => {
    setopenSubmit(false)
    setIsOpen(true)
  }

  const closeModal = () => {
    setIsOpen(false)
    props.resetId()
  }

  const openSubmitScreen1 = (appId) => {
    console.log("app id => ", appId)
    setappId(appId)
    setheading("Clerical Objection")
    closeModal()
    setopenSubmit(true)
  }

  const openSubmitScreen2 = (appId) => {
    setappId(appId)
    closeModal()
    setopenSubmit(true)
    setheading("Assessment Objection")
  }

  const openSubmitScreen3 = (appId) => {
    setappId(appId)
    closeModal()
    setopenSubmit(true)
    setheading("Forgery Objection")
  }
  const afterOpenModal = () => { }

  useEffect(() => {
    console.log('modal enter obj => ', props?.modalAction)
    {
      ((props?.modalAction == 1) || (props?.modalAction == 2) || (props?.modalAction == 3)) && openModal()
    }
  }, [props?.modalAction])

  return (
    <>
      {/* ===============Modal============================ */}
      <Modal
        isOpen={modalIsOpen}
        onAfterOpen={afterOpenModal}
        className="z-20 h-screen w-screen backdrop-blur-sm flex flex-row justify-center items-center overflow-auto"
        contentLabel="Example Modal"
      >

        <div class=" rounded-lg shadow-lg shadow-indigo-300 md:w-[73%] sm:h-[85vh] w-full relative border-2 border-indigo-500 bg-gray-50 px-6 py-4 h-max border-t-2 border-l-2 overflow-auto animate__animated animate__slideInLeft animate__faster" >

          <div className="absolute top-2 z-10 bg-red-200 hover:bg-red-300 right-2 rounded-full p-2 cursor-pointer" onClick={closeModal}>
            <ImCross fontSize={10} />
          </div>

          {
            props?.modalAction == 1 && <ObjectionRectificationTable closePopUp={closeModal} id={props?.id} submitForm={openSubmitScreen1} />
          }
          {
            props?.modalAction == 2 && <div><ObjectionFormIndex closePopUp={closeModal} id={props?.id} submitForm={openSubmitScreen2} /></div>
          }
          {
            props?.modalAction == 3 && <ObjectionForgery closePopUp={closeModal} id={props?.id} submitForm={openSubmitScreen3} />
          }
        </div>

      </Modal>

      <ApplicationSubmitScreen heading={heading} appNo={appId} openSubmit={openSubmit} refresh={() => setindex(index + 1)} />

    </>
  )

}

export default ObjectionIndex